.skills_container {
  background-image: url("./../../Images/background_image_1.png");
  height: fit-content;
  width: 100vw;
  background-attachment: fixed;
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  border-top: 3px ridge #DCABA0;
}

.cards_title p {
  font-family: 'Raleway', sans-serif;
  font-size: 1.5em;
  line-height: 3;
  padding: 1em;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.skills_title {
  text-align: center;
  height: 3.5em;
  border-bottom: 1px solid #EECF7C;
  
}

.skills_title h2 {
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}

.skills_title hr {
  width: 15em;
  background-color: #44cfcb;
  color: #44cfcb;
}

.hide {
  position: absolute;
  bottom: 0;
  right: 0;
  background: black;
  color: white; 
  font-size: 0.7rem;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: visibility 0s, opacity 0.5s linear;
  transition: visibility 0s, opacity 0.5s linear;
  text-align: center;
}

.icon:hover + .hide {
  display: block;
}

.icon_container {
  position: relative;
  float: left;
  margin: 1.5em;
}

.icon_container:hover {
  cursor: pointer;
}

.icon_container:hover div {
  width: 5rem;
  height: auto;
  /* padding: 8px 15px; */
  visibility: visible;
  opacity: 0.7;
}

/* Media Queries */

@media only screen and (max-width: 450px) {
  .box_container {
    height: fit-content;
    
  }
  .tech_hide hr {
    width: 10em;
  }
  
}